import { Block } from '@ali/4ever-cangjie';
import { Card } from '@ali/4ever-mo';
import { ILinkCardInfo } from '../../types';
import buildLinkCardData from './buildLinkCardData';


export default function buildLinkCardBlock(data: { text: string, href: string, cardInfo: ILinkCardInfo }) {
  const payload = buildLinkCardData(data);

  return Block.create({
    type: Card.TYPE,
    data: {
      ...payload,
    },
  });
}